<template>
  <div class="g-merchant scroll-list-wrap">
    <cube-scroll
            ref="scroll"
            :data="list"
            :options="options"
            @pulling-down="onPullingDown"
            @pulling-up="onPullingUp">
      <div class="m-navChoose">
        <div @click="showProv">{{area.prov.str||'请选择省份'}}</div>
        <div @click="showCity">{{area.city.str||'请选择城市'}}</div>
        <div @click="showArea">{{area.area.str||'请选择区域'}}</div>
        <div @click="showcategory">{{area.sortId.str||'请选择类目'}}</div>
        <input type="text" placeholder="请输入店铺名" v-model="bit.keyword">
        <div class="s-search" @click="merchant(bit,1)">搜索</div>
      </div>
      <div class="m-list">
        <router-link v-for="(v,i) in  list" :key="i" :to="{name:'merchantDetails',query:{username:v.username}}" tag="div" class="s-box">
          <div class="img">
            <img v-lazy="'https://fx18.oss-cn-hangzhou.aliyuncs.com/'+v.icon" alt="">
          </div>
          <div class="name">{{v.name}}</div>
        </router-link>
      </div>
    </cube-scroll>
  </div>
</template>

<script>
  import {provinceList, cityList, areaList} from '../assets/js/area'

  const addressData = provinceList
  addressData.forEach(province => {
    province.children = cityList[province.value]
    province.children.forEach(city => {
      city.children = areaList[city.value]
    })
  })
  export default {
    name: "merchant",
    data() {
      return {
        area: {
          /*区域配置*/
          prov: {str: '', i: ''},
          city: {str: '', i: ''},
          area: {str: '', i: ''},
          sortId:{str:'',i:''}
        },
        sorts:[],/*类目数据*/
        list: [],/*商品数据*/
        options: {
          /*滚动配置*/
          scrollbar: {fade: true},
          pullUpLoad: {txt: {noMore: '没有更多数据了'}},
          pullDownRefresh: {stopTime: 0, txt: ' '}
        },
        bit:{
          keyword:'',
          page:1,
          province:'',
          city:'',
          area:'',
          sortId:''
        }
      }
    },
    created(){
      this.merchant(this.bit);//联盟商家列表
      this.sorting();/*经营类目*/
    },
    methods: {
      sorting(){
        this.$post(this.$bee+'/api/shop/listSort').then((data)=>{
          if(data.code==200){
            data.data.sorts.forEach((i)=> {
              this.sorts.push({text:i.name,value:i.id});
            })
          }else{
            this.$createToast({
              txt: data.message,
              type: 'txt'
            }).show();
          }
        })
      },
      merchant(_t,_s){
        if(_s){//是否为搜索
          this.list=[];
          _t.page=1;
        }
        this.$post(this.$bee+'/api/shop/listLeagueShop',_t).then((data)=>{
          if(data.code==200&&data.data.shops.length!=0){

            data.data.shops.forEach((i)=> {
              this.list.push(i);
            })
            this.bit.page++;
          }else{
            this.$refs.scroll.forceUpdate()
          }
        })
      },
      onPullingDown() {/*下拉刷新*/
        this.bit.page=1;
        this.list=[];
        this.merchant(this.bit);
      },
      onPullingUp() {/*上拉加载*/
        this.merchant(this.bit);
      },
      showProv() {/*请选择省份*/
        var _this = this;
        this.$createPicker({
          title: '请选择省份',
          data: [addressData],
          onSelect(v, i, t) {
            _this.area.prov.str = t.join(', ');
            _this.area.prov.i = i;

            _this.area.city.str = '';
            _this.area.city.i = '';
            _this.area.area.str = '';
            _this.area.area.i = '';
            _this.bit.province=t.join(', ');
          },
          onCancel() {
            console.log('取消')
          }
        }).show()
      },
      showCity() {/*请选择城市*/
        var _this = this;
        if(!addressData[_this.area.prov.i]){
          this.$createToast({
            txt: '请先选择省份',
            type: 'txt'
          }).show();
          return ;
        }
        this.$createPicker({
          title: '请选择城市',
          data: [addressData[_this.area.prov.i].children],
          onSelect(v, i, t) {
            _this.area.city.str = t.join(', ');
            _this.area.city.i = i;

            _this.area.area.str = '';
            _this.area.area.i = '';
            _this.bit.city=t.join(', ');
          },
          onCancel() {
            console.log('取消')
          }
        }).show()
      },
      showArea() {/*请选择区域*/
        var _this = this;
        if(!addressData[_this.area.prov.i]){
          this.$createToast({
            txt: '请先选择省份',
            type: 'txt'
          }).show();
          return ;
        }else if(!addressData[_this.area.prov.i].children[_this.area.city.i]){
          this.$createToast({
            txt: '请先选择城市',
            type: 'txt'
          }).show();
          return ;
        }
        this.$createPicker({
          title: '请选择区域',
          data: [addressData[_this.area.prov.i].children[_this.area.city.i].children],
          onSelect(v, i, t) {
            _this.area.area.str = t.join(', ');
            _this.area.area.i = i;
            _this.bit.area=t.join(', ');
          },
          onCancel() {
            console.log('取消')
          }
        }).show()
      },
      showcategory(){
        var _this = this;
        this.$createPicker({
          title: '请选择区域',
          data: [_this.sorts],
          onSelect(v, i, t) {
            _this.area.sortId.str = t.join(', ');
            _this.area.sortId.i = i;
            _this.bit.sortId=v[0];
          },
          onCancel() {
            console.log('取消')
          }
        }).show()
      }
    }
  }
</script>

<style scoped lang="stylus">
  .g-merchant
    padding-bottom 50px
    .m-navChoose
      padding 10px 7px 0 7px
      display flex
      flex-wrap wrap
      & > div, & > input
        width: 110px;
        height: 25px;
        line-height 25px
        margin 5px
        font-size 15px
        color: #4d4d4d;
        text-align left
        text-indent 4px
        background-image url(../assets/images/bottom-arrow.png)
        background-repeat no-repeat
        background-size 12px 6px
        background-position 97% center
        background-color: #e5e5e5;
      & > input
        background: #e5e5e5;
      .s-search
        background: #cf3535;
        color: #4d4d4d;
        text-align center
        text-indent 0
    .m-list
      padding 14px 7px 0 7px
      display flex
      flex-wrap wrap
      .s-box
        margin 0 5px 10px
        width: 170px;
        height: 230px;
        background-color: #ffffff;
        box-shadow: 0px 5px 14px 2px rgba(84, 84, 85, 0.3);
        .img
          width 170px
          height 170px
          img
            width 100%
            height 100%
        .name
          padding 8px 6px 0
          color: #5c5b5b;
          font-size: 13px;
          text-align left
          line-height 1.5
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
          overflow hidden
          height auto
          box-sizing border-box
</style>
